import './style.css'
import * as THREE from 'three'
import Stats from 'three/examples/jsm/libs/stats.module.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GUI } from 'dat.gui'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import { DDSLoader } from 'three/examples/jsm/loaders/DDSLoader.js';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader.js';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { SAOPass } from 'three/examples/jsm/postprocessing/SAOPass.js';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
//import { FBXLoader } from './jsm/loaders/FBXLoader.js';

// import { RenderPass } from './jsm/postprocessing/RenderPass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
import { CopyShader } from 'three/examples/jsm/shaders/CopyShader.js';
import { FXAAShader } from 'three/examples/jsm/shaders/FXAAShader.js';
import { LineBasicMaterial } from 'three';

const cubeTextureLoader = new THREE.CubeTextureLoader()

let container, stats, controls;
let camera, scene, renderer;
let composer, renderPass, saoPass;
let group;
let loader, gltf;
let interior = [];
let pano1, pano2, pano3;

let composer1, composer2, fxaaPass;

init();
animate();

function init() {

	/**
* Update all materials
*/
	const updateAllMaterials = () => {
		scene.traverse((child) => {
			if (child instanceof THREE.Mesh && !(child.material instanceof THREE.MeshStandardMaterial)) // && (child.material instanceof THREE.MeshPhongMaterial || child.material instanceof THREE.MeshStandardMaterial)
			{
				// console.log(child)
				// child.material.envMap = environmentMap
				child.material.envMapIntensity = 15
				// child.material.emissive = true
				// child.emissive = true
				child.material.needsUpdate = true
				child.castShadow = true
				child.receiveShadow = true
				child.side = THREE.DoubleSide

				// ***** Clipping setup (material): *****
				child.clippingPlanes = [localPlane]
				child.clipShadows = true
				// console.log(child.material)
			}
		})
	}

	const updateAllMaterials2 = () => {
		scene.traverse((child) => {
			if (child instanceof THREE.Mesh && !(child.material instanceof THREE.MeshStandardMaterial)) // && (child.material instanceof THREE.MeshPhongMaterial || child.material instanceof THREE.MeshStandardMaterial)
			{
				// console.log(child)
				// child.material.envMap = environmentMap
				child.material.envMapIntensity = 15
				child.material.transparent = true
				// child.material.emissive = true
				// child.emissive = true
				child.material.needsUpdate = true
				child.castShadow = false
				child.receiveShadow = false
				child.side = THREE.DoubleSide
				child.visible = true


				// ***** Clipping setup (material): *****
				child.clippingPlanes = [localPlane]
				child.clipShadows = true
			}
			for (let i = 0; i < interior.length; i++) {
				interior[i].visible = true
			}
		})
	}

	/**
	 * Environment map
	 */
	// const environmentMap = cubeTextureLoader.load([
	//     '/textures/environmentMaps/0/px.jpg',
	//     '/textures/environmentMaps/0/nx.jpg',
	//     '/textures/environmentMaps/0/py.jpg',
	//     '/textures/environmentMaps/0/ny.jpg',
	//     '/textures/environmentMaps/0/pz.jpg',
	//     '/textures/environmentMaps/0/nz.jpg'
	// ])

	// environmentMap.encoding = THREE.sRGBEncoding

	// environment.background = environmentMap

	// Canvas
	const canvas = document.querySelector('canvas.webgl')

	// container = document.createElement( 'div' );
	// document.body.appendChild( container );

	const localPlane = new THREE.Plane(new THREE.Vector3(0, - 1, 0), 0.8);
	const globalPlane = new THREE.Plane(new THREE.Vector3(0, -1, 0), 1);

	const width = window.innerWidth || 1;
	const height = window.innerHeight || 1;
	const devicePixelRatio = window.devicePixelRatio || 1;

	renderer = new THREE.WebGLRenderer({ antialias: true, canvas: canvas });
	renderer.setClearColor(0x000000);
	renderer.setPixelRatio(devicePixelRatio);
	// renderer.background = new THREE.Color(0xffffff)
	renderer.setSize(width, height);

	renderer.physicallyCorrectLights = true
	renderer.outputEncoding = THREE.sRGBEncoding
	// renderer.toneMapping = THREE.ACESFilmicToneMapping
	renderer.toneMapping = THREE.LinearToneMapping
	renderer.toneMappingExposure = 2.2
	renderer.shadowMap.enabled = true
	renderer.shadowMap.type = THREE.PCFSoftShadowMap
	const globalPlanes = [globalPlane],
		Empty = Object.freeze([]);

	renderer.clippingPlanes = Empty; // GUI sets it to globalPlanes
	renderer.localClippingEnabled = true;
	// document.body.appendChild( renderer.domElement );

	camera = new THREE.PerspectiveCamera(65, width / height, 0.05, 100);
	camera.position.x = 2.5;
	camera.position.y = 2.5;
	camera.position.z = 4.5;



	// Controls
	controls = new OrbitControls(camera, canvas)
	controls.enableDamping = true
	controls.target = new THREE.Vector3(-2.5, 1, -2)

	scene = new THREE.Scene();
	scene.background = new THREE.Color(0xffffff)
	// scene.environment = environmentMap

	group = new THREE.Object3D();
	scene.add(group);

	const directionalLight = new THREE.DirectionalLight('#ffeedd', 0.7)
	directionalLight.castShadow = true
	directionalLight.shadow.camera.far = 25
	directionalLight.shadow.mapSize.set(1024, 1024)
	directionalLight.shadow.normalBias = 0.005
	// directionalLight.shadowmap
	directionalLight.position.set(-1.3, 5, 2.6)
	scene.add(directionalLight)

	const light = new THREE.PointLight(0xffdddd, 8.9);
	light.position.z = -10;
	light.position.y = 3;
	light.position.x = 10;
	scene.add(light);

	const light1 = new THREE.PointLight(0xffffff, 8.9);
	light1.position.z = -10;
	light1.position.x = -10;
	light1.position.y = 3;
	// scene.add( light1 );

	const light2 = new THREE.PointLight(0xddddff, 8.9);
	light2.position.z = 10;
	light2.position.x = - 10;
	light2.position.y = 3;
	// scene.add( light2 );

	const light3 = new THREE.PointLight(0xddffdd, 8.9);
	light3.position.z = 10;
	light3.position.x = 10;
	light3.position.y = 3;
	// scene.add( light3 );

	// {const geometry1 = new THREE.SphereBufferGeometry(.1, 128, 128);
	// 	// invert the geometry on the x-axis so that all of the faces point inward
	// 	geometry1.scale(- 1, 1, 1);

	// 	const texture = new THREE.TextureLoader().load('textures/Pano11.jpg');
	// 	const material = new THREE.MeshStandardMaterial({ map: texture, envMapIntensity: .1, lightMapIntensity: .1, emissiveIntensity: 0.5});

	// 	pano1 = new THREE.Mesh(geometry1, material);
	// 	pano1.rotateY(-3.1415926/2)
	// 	pano1.position.set(-372.8168/100, 72.48/100, -357.819/100)

	// 	scene.add(pano1);
	// }

	// {const geometry1 = new THREE.SphereBufferGeometry(.1, 128, 128);
	// 	// invert the geometry on the x-axis so that all of the faces point inward
	// 	geometry1.scale(- 1, 1, 1);

	// 	const texture = new THREE.TextureLoader().load('textures/Pano1.jpg');
	// 	const material = new THREE.MeshStandardMaterial({ map: texture, envMapIntensity: .1, lightMapIntensity: .1, emissiveIntensity: 0.5});

	// 	pano2 = new THREE.Mesh(geometry1, material);
	// 	pano2.rotateY(-3.1415926/2)
	// 	pano2.position.set(-102.019/100, 72.48/100, -357.59/100)

	// 	scene.add(pano2);
	// }

	// const light4 = new THREE.AmbientLight( 0xffffff, 5 );
	// scene.add( light4 );

	const light5 = new THREE.AmbientLight(0xffffff, 1.2);
	scene.add(light5);

	const geometry = new THREE.BoxGeometry(3, 48, 24);

	// model
	const loader = new FBXLoader();
	loader.load( 'models/site_austin1.fbx', function ( object ) {
		object.scale.x = 0.01
		object.scale.y = 0.01
		object.scale.z = 0.01
		object.position.y += 0.01
		for(let i = 0; i < object.children[0].children.length; i++){
			object.children[0].children[i].material = new THREE.LineBasicMaterial({
				color: 0x000000,
				linewidth: 1,
				linecap: 'round', //ignored by WebGLRenderer
				linejoin:  'round' //ignored by WebGLRenderer
			})
		}
		console.log(object.children[0].children)
		object.material = new THREE.LineBasicMaterial({
			color: 0x000000,
			linewidth: 1,
			linecap: 'round', //ignored by WebGLRenderer
			linejoin:  'round' //ignored by WebGLRenderer
		})
		scene.add( object );

	} );

	const loader1 = new FBXLoader();
	loader1.load( 'models/site_austin_srf.fbx', function ( object ) {
		object.scale.x = 0.01
		object.scale.y = 0.01
		object.scale.z = 0.01
		scene.add( object );

	} );


	const onProgress = function (xhr) {

		if (xhr.lengthComputable) {

			const percentComplete = xhr.loaded / xhr.total * 100;
			console.log(Math.round(percentComplete, 2) + '% downloaded');

		}

	};

	const onError = function () { };

	const manager = new THREE.LoadingManager();
	manager.addHandler(/\.dds$/i, new DDSLoader());

	// comment in the following line and import TGALoader if your asset uses TGA textures
	// manager.addHandler( /\.tga$/i, new TGALoader() );

	new MTLLoader(manager)
		.setPath('models/BaltimoreObj/')
		.load('new2.mtl', function (materials) {


			materials.preload();

			new OBJLoader(manager)
				.setMaterials(materials)
				.setPath('models/BaltimoreObj/')
				.load('new2.obj', function (object) {
					// .setPath( 'models/BaltText/' )
					// .load( 'baltExt.obj', function ( object ) {

					// object.position.y = - 8.95;
					// object.position.x = 2.20
					object.scale.x = 0.01
					object.scale.y = 0.01
					object.scale.z = 0.01
					scene.add(object);
					updateAllMaterials()

				}, onProgress, onError);

		});


	new MTLLoader(manager)
		.setPath('models/BaltText/')
		.load('baltInt.mtl', function (materials) {

			materials.preload();

			new OBJLoader(manager)
				.setMaterials(materials)
				.setPath('models/BaltText/')
				.load('baltInt.obj', function (object) {

					object.position.y = 0.01;
					// object.position.x = 2.20
					object.scale.x = 0.01
					object.scale.y = 0.01
					object.scale.z = 0.01
					object.visible = false;
					interior.push(object)
					scene.add(object);


				}, onProgress, onError);

		});





	// stats = new Stats();
	// container.appendChild( stats.dom );

	fxaaPass = new ShaderPass(FXAAShader);
	// const copyPass = new ShaderPass( CopyShader );

	composer = new EffectComposer(renderer);
	renderPass = new RenderPass(scene, camera);
	composer.addPass(renderPass);
	composer.addPass(fxaaPass);

	const pixelRatio = renderer.getPixelRatio();
	var uniforms = fxaaPass.material.uniforms;
	uniforms['resolution'].value.x = 1 / (window.innerWidth * pixelRatio);
	uniforms['resolution'].value.y = 1 / (window.innerHeight * pixelRatio);
	saoPass = new SAOPass(scene, camera, false, true);
	// saoPass.params.saoBias = 0.06
	// saoPass.params.saoIntensity = 0.34
	// saoPass.params.saoScale = 10
	// saoPass.params.saoKernelRadius = 2
	// saoPass.params.saoBlurRadius = 0.5
	// saoPass.params.saoBlurStdDev = 40.7
	// saoPass.params.saoBlurDepthCutoff = 0.055
	saoPass.params.saoBias = -1
	saoPass.params.saoIntensity = 0.1
	saoPass.params.saoScale = 10
	saoPass.params.saoKernelRadius = 12
	saoPass.params.saoBlurRadius = 3
	saoPass.params.saoBlurStdDev = 1.5
	saoPass.params.saoBlurDepthCutoff = 0.002

	// saoPass.params.saoBias = -1
	// saoPass.params.saoIntensity = 0.14
	// saoPass.params.saoScale = 10
	// saoPass.params.saoKernelRadius = 7
	// saoPass.params.saoBlurRadius = 0
	// saoPass.params.saoBlurStdDev = 36
	// saoPass.params.saoBlurDepthCutoff = 0
	composer.addPass(saoPass);

	// var pixelRatio = renderer.getPixelRatio();
	// var uniforms = saoPass.material.uniforms;

	// uniforms[ 'resolution' ].value.x = 1 / ( window.innerWidth * pixelRatio );
	// uniforms[ 'resolution' ].value.y = 1 / ( window.innerHeight * pixelRatio );

	// Init gui
	const gui = new GUI();
	gui.add(saoPass.params, 'output', {
		'Beauty': SAOPass.OUTPUT.Beauty,
		'Beauty+SAO': SAOPass.OUTPUT.Default,
		'SAO': SAOPass.OUTPUT.SAO,
		'Depth': SAOPass.OUTPUT.Depth,
		'Normal': SAOPass.OUTPUT.Normal
	}).onChange(function (value) {

		saoPass.params.output = parseInt(value);

	});
	gui.add(saoPass.params, 'saoBias', - 1, 1);
	gui.add(saoPass.params, 'saoIntensity', 0, 1);
	gui.add(saoPass.params, 'saoScale', 0, 10);
	gui.add(saoPass.params, 'saoKernelRadius', 1, 100);
	gui.add(saoPass.params, 'saoMinResolution', 0, 1);
	gui.add(saoPass.params, 'saoBlur');
	gui.add(saoPass.params, 'saoBlurRadius', 0, 200);
	gui.add(saoPass.params, 'saoBlurStdDev', 0.5, 150);
	gui.add(saoPass.params, 'saoBlurDepthCutoff', 0.0, 0.1);

	gui.add(directionalLight, 'intensity').min(0).max(10).step(0.001).name('lightIntensity')
	gui.add(directionalLight.position, 'x').min(- 5).max(5).step(0.001).name('lightX')
	gui.add(directionalLight.position, 'y').min(- 5).max(5).step(0.001).name('lightY')
	gui.add(directionalLight.position, 'z').min(- 5).max(5).step(0.001).name('lightZ')

	gui
		.add(renderer, 'toneMapping', {
			No: THREE.NoToneMapping,
			Linear: THREE.LinearToneMapping,
			Reinhard: THREE.ReinhardToneMapping,
			Cineon: THREE.CineonToneMapping,
			ACESFilmic: THREE.ACESFilmicToneMapping
		})
		.onFinishChange(() => {
			renderer.toneMapping = Number(renderer.toneMapping)
			updateAllMaterials()

		})
	gui.add(renderer, 'toneMappingExposure').min(0).max(10).step(0.001)
	let folderLocal = gui.addFolder('Local Clipping'),
		propsLocal = {

			get 'Enabled'() {

				return renderer.localClippingEnabled;

			},
			set 'Enabled'(v) {

				renderer.localClippingEnabled = v;

			},

			// get 'Shadows'() {

			// 	return material.clipShadows;

			// },
			// set 'Shadows'( v ) {

			// 	material.clipShadows = v;

			// },

			get 'Plane'() {

				return localPlane.constant;

			},
			set 'Plane'(v) {

				localPlane.constant = v;

			}

		},

		folderGlobal = gui.addFolder('Global Clipping'),
		propsGlobal = {

			get 'Enabled'() {
				updateAllMaterials2()
				// saoPass.params.saoBlurRadius = 2	
				// saoPass.params.saoBlurStdDev = 2

				return renderer.clippingPlanes !== Empty;

			},
			set 'Enabled'(v) {

				renderer.clippingPlanes = v ? globalPlanes : Empty;

			},

			get 'Plane'() {

				return globalPlane.constant;

			},
			set 'Plane'(v) {

				globalPlane.constant = v;

			}

		};

	folderLocal.add(propsLocal, 'Enabled');
	// folderLocal.add( propsLocal, 'Shadows' );
	folderLocal.add(propsLocal, 'Plane', 0.3, 1.25);

	folderGlobal.add(propsGlobal, 'Enabled');
	folderGlobal.add(propsGlobal, 'Plane', - 0.4, 5);

	gui.hide()

	window.addEventListener('resize', onWindowResize);

}

function onWindowResize() {

	const width = window.innerWidth || 1;
	const height = window.innerHeight || 1;

	camera.aspect = width / height;
	camera.updateProjectionMatrix();
	renderer.setSize(width, height);

	composer.setSize(width, height);


}

function animate() {

	requestAnimationFrame(animate);

	controls.update()

	// stats.begin();
	render();
	// stats.end();


}

function render() {

	const timer = performance.now();
	// group.rotation.x = timer * 0.0002;
	// group.rotation.y = timer * 0.0001;

	composer.render();


}